AA,0GAEA,WACE,CAAA,UACA,CAAA,aAOV,YACE,CAAA,qBACA,CAAA,kBACA,CAAA,aACA,CAAA,QACA,CAAA,8BAEA,YACE,CAAA,sBACA,CAAA,qBACA,CAAA,QACA,CAAA,gCAGF,oBACE,CAAA,iDACA,wBACE,CAAA,+BAIJ,sBACE,CAAA,gCAGF,kBACE,CAAA,+BAGF,eACE,CAAA,8BAGF,kBACE,CAAA,+BAGF,YACE,CAAA,kBACA,CAAA,cACA,CAAA,sBACA,CAAA,kBACA,CAAA,qBACA,CAAA,iBACA,CAAA,qBACA,CAAA,qBACA,CAAA,YACA,CAAA,iBACA,CAAA,kBACA,CAAA,kBAEA,CAAA,oBACA,CAAA,iBACA,CAAA,YACA,CAAA,0DAEA,iBACE,CAAA,4DAGF,gBACE,CAAA,8DAGF,cACE,CAAA,cACA,CAAA,UACA,CAAA,eACA,CAAA,kCAOF,aACE,CAAA,UACA,CAAA,kBACA,CAAA,qDAIJ,cACE,CAAA,uBACA,CAAA,iBACA,CAAA,YACA,CAAA,sBACA,CAAA,kBACA,CAAA,gBACA,CAAA,4EAEA,aACE,CAAA,8EAGF,oBACE,CAAA,gBACA,CAAA,gBACA,CAAA,yGAEA,WACE,CAAA,wBACA,CAAA,gFAIJ,wBACE,CAAA,uGAGF,wBACE,CAAA,yPAGF,wBAEE,CAAA,oZAIA,cACE,CAAA,gBACA,CAAA,gBACA,CAAA,iBACA,CAAA,OACA,CAAA,QACA,CAAA,kBACA,CAAA,gWAIA,YACE,CAAA,wbAKF,OACE,CAAA,QACA,CAAA,yGAMJ,oBACE,CAAA,0IAGF,wDAEE,CAAA,uBACA,CAAA,2BACA,CAAA,QACA,CAAA,QACA,CAAA,iBACA,CAAA,0BACA,CAAA,SACA,CAAA,UACA,CAAA,oBACA,CAAA,gFAIJ,qBACE,CAAA,UACA,CAAA,kBACA,CAAA,UACA,CAAA,mBACA,CAAA,uGAEA,WACE,CAAA,iDAKN,UACE,CAAA,iBACA,CAAA,kBACA,CAAA,eACA,CAAA,kBACA,CAAA,WACA,CAAA,mDAGF,WACE,CAAA,WACA,CAAA,WACA,CAAA,iDAGF,sBACE,CAAA,WACA,CAAA,WACA,CAAA,gDAGF,WACE,CAAA,WACA,CAAA,WACA,CAAA,mDAGF,WACE,CAAA,WACA,CAAA,YACA,CAAA,iDAGF,sBACE,CAAA,YACA,CAAA,YACA,CAAA,gDAGF,WACE,CAAA,YACA,CAAA,YACA,CAAA,gDAGF,WACE,CAAA,YACA,CAAA,gBACA,CAAA,YACA,CAAA,wBAIJ,YACE,CAAA,sBACA,CAAA,gBACA,CAAA,eAMA,CAAA,yCAJA,kBACE,CAAA,0CAMA,aACE,CAAA,cACA,CAAA,UACA,CAAA,gEAGF,eACE,CAAA,UACA,CAAA,eACA,CAAA,eACA,CAAA,aACA,CAAA,cACA,CAAA,gBACA,CAAA,aACA,CAAA,qBACA,CAAA,kEAEA,aACE,CAAA,yFAGF,WACE,CAAA,iCACA,CAAA,+FAEA,8BACE,CAAA,yFAIJ,WACE,CAAA,wBACA,CAAA,uFAGF,wBACE,CAAA,kHAGF,YACE,CAAA,qEAGF,cACE,CAAA,qBAMR,WACE,CAAA,wBACA,CAAA,WACA,CAAA,wBACA,CAAA,aACA,CAAA,iBACA,CAAA,eACA,CAAA,WACA,CAAA,oEAGF,iBACE,CAAA,2CAGF,UACE,CAAA,iBACA,CAAA,KACA,CAAA,MACA,CAAA,OACA,CAAA,QACA,CAAA,qCACA,CAAA,mBACA,CAAA,kEAGF,UACE,CAAA,iBACA,CAAA,KACA,CAAA,MACA,CAAA,OACA,CAAA,QACA,CAAA,qCACA,CAAA,mBACA,CAAA,yBAGF,aACE,CAAA,MAGF,eACE,CAAA,gBACA,CAAA,iBACA,CAAA,iBACA,CAAA,wCAEA,kBACE,CAAA,8CAGF,kBACE,CAAA,oBAIJ,cACE,CAAA,2CAEA,cACE","sourcesContent":["#_canopy {\n  margin-top: 20px;\n  overflow-anchor: none; // Prevent browser from trying to maintain position when page contents change, which might interfere with window.scrollTo\n\n  /* Removing DOM elements from the bottom of the screen can force the page to scroll upward to the existing elements\n  which is not animated. If we keep a padding on the bottom of the last element just beyond the viewport, then\n  removal of elements from the bottom of the visible stack never brings the real bottom of the page into view, and so\n  the scroll upwards can be animated using the window.scrollTo method. */\n\n  padding-bottom: calc(90vh);\n  min-height: calc(110vh); // to avoid scroll bar appearing when page length changes for small content that doesn't need scrolling\n\n  min-width: 700px; // trying to prevent content squish when page goes narrow\n  font-family: \"Times New Roman\", Times, serif; // good default and standardizes layout for browser tests\n}\n\nbody {\n  overflow-x: auto; // trying to prevent content squish when page goes narrow\n}\n\nh1.canopy-header {\n  text-align: center;\n  margin: auto;\n  margin-top: 20px;\n  max-width: 750px;\n  word-break: break-word;\n  hyphens: auto;\n}\n\nhr.canopy-hr {\n  width: 120px;\n  display: block;\n  height: 1px;\n  border: 0;\n  border-top: 5px solid #ccc;\n  margin: 0px auto;\n}\n\nh1 + section.canopy-topic-section {\n  margin-top: 30px;\n}\n\nsection.canopy-topic-section {\n  margin-top: 32px; // has to match .canopy-topic-section hr + p\n}\n\nsection.canopy-section {\n  margin-top: 32px;\n  unicode-bidi: unset;\n}\n\n.canopy-topic-section hr + p {\n  margin-top: 27px;\n}//\n\np.canopy-paragraph {\n  overflow: visible; // In case external link icon is at end of line\n  font-size: 20px;\n  max-width: 750px;\n  overflow-wrap: break-word; // this causes eg table cells to break long lines into rows\n  //word-break: break-word; // this separates words in table cells\n  //hyphens: auto; // not sure we want this, rather put whole word on next line?\n  margin: auto;\n\n  line-height: 1.3;\n  letter-spacing: -.003em;\n  font-weight: 400;\n\n  position: relative; // to allow absolute children\n  //unicode-bidi: plaintext; // We want unicode bidi characters to allow overriding character direction which plaintext doesn't allow\n}//\n\n.canopy-topic-section hr + p {\n  margin-top: 31px; // 35px minus 1px bar thickness\n}\n\ncode.canopy-code-block {\n  font-family: 'Courier New', Courier, monospace; /* Monospaced font for code */\n  background-color: #eee; /* Light background to highlight code */\n  padding: 2px 4px; /* Small padding around the code */\n  border-radius: 3px; /* Optional: rounded corners */\n  border: .5px solid #ddd; /* Optional: light border */\n  color: #5a5a5a; /* Subtle dark gray color for the code text, less harsh than black */\n}\n\n// Tool tip\n\n.canopy-tooltip {\n  position: relative;\n  display: inline-block;\n  cursor: pointer;\n\n  .canopy-tooltip-icon {\n    font-size: 11px;\n  }\n\n  .canopy-tooltiptext {\n    visibility: hidden;\n    width: 200px;\n    background-color: white;\n    color: black;\n    text-align: center;\n    border-radius: 6px;\n    border: 1px solid black;\n    padding: 10px;\n    position: absolute;\n    z-index: 1;\n    top: 50%;\n    left: 105%;\n    margin-left: 10px;\n    transform: translateY(-50%);\n    opacity: 0;\n    font-size: 14px;\n  }\n\n  &:hover .canopy-tooltiptext {\n    visibility: visible;\n    opacity: 1;\n  }\n}\n\n// Text\n.canopy-text-span {\n  display: inline;\n}\n\n.canopy-linebreak-span {\n  display: block;\n  margin-bottom:12px;\n}\n\n///////////// Link styles /////////////////\n\na.canopy-selectable-link { // even menu and table cells\n  text-decoration: none;\n  color: black;\n  cursor: pointer;\n\n  &:focus {\n    outline: 0;\n  }\n}\n\na.canopy-selectable-link,\na.canopy-disabled-link {\n  padding-left: 11px; // For RTL text\n  margin-left: -11px;\n\n  white-space: nowrap; // this prevents gap element non-breaking spaces from wrapping, which causes link content to break early with subsequent punctuation\n\n  .canopy-link-container { // putting bg on inner container lets outer wrapper have non-bg padding\n    background-color: #FFFFFF;\n\n    padding-left: 2px; // gives button padding inside container on the sides to match natural line height\n    padding-right: 2px;\n\n    border-radius: 3px;\n    border: 1px solid #dddddd;\n\n    unicode-bidi: inherit; // prevents dir=auto from making RTL text align left\n\n    white-space: nowrap; // Don't separate icon from last link content word\n\n    .canopy-link-content-container {\n      display: inline;\n\n      white-space: normal; // allow breaks between words just not between last word and icon\n    }\n  }\n\n  // shared multi-line layout\n  &:first-child:has(+ .canopy-linebreak-span),\n  .canopy-linebreak-span + &:has(+ .canopy-linebreak-span),\n  .canopy-linebreak-span + &:last-child,\n  &:only-child {\n    &.canopy-multiline-link .canopy-link-container {\n      display: inline-block;\n    }\n  }\n}\n\na.canopy-selectable-link {\n  .canopy-link-container {\n    &:hover {\n      background-color: #f5f6fe;\n    }\n  }\n\n  &.canopy-selected-link, &.canopy-selected-link.canopy-open-link, &.canopy-selected-link.canopy-open-link code { // open and selected\n    .canopy-link-container {\n      background-color: #edeefc;\n      border: 1px solid #e2e2e2;\n      text-decoration: none;\n    }\n  }\n\n  &.canopy-open-link .canopy-link-container { // just open\n    background-color: #e1dfdf;\n    border: 1px solid #cbcaca;\n    border-radius: 1px;\n    unicode-bidi: inherit;\n  }\n\n  .canopy-external-link-icon, .canopy-forward-cycle-icon, .canopy-back-cycle-icon, .canopy-up-cycle-icon {\n    margin-left: -4px;\n    &::before {\n      content: \"\\00a0\"; // NBSP: prevents break between last word and icon\n    }\n  }\n\n  .canopy-link-icon-gap {\n    margin-right: -12px;\n    font-size: 5px;\n    user-select: none;\n    &::before {\n      content: \"\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\";\n    }\n  }\n\n  .canopy-link-terminal-gap {\n    margin-right: -12px;\n    font-size: 5px;\n    user-select: none;\n    &::before {\n      content: \"\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\\202F\";\n    }\n  }\n\n  &.canopy-external-link .canopy-link-container .canopy-external-link-icon {\n    /* Open Iconic Icon Set, MIT License, https://iconduck.com/icons/53818/external-link */\n    background-image: url(data:image/svg+xml;base64,PHN2ZyBoZWlnaHQ9IjgiIHZpZXdCb3g9IjAgMCAxMCA4IiB3aWR0aD0iOSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KICA8cGF0aCBkPSJNMSAwdjhoOHYtMmgtMXYxaC02di02aDF2LTF6bTQgMCAxLjUgMS41LTIuNSAyLjUgMSAxIDIuNS0yLjUgMS41IDEuNXYtNHoiIHRyYW5zZm9ybT0idHJhbnNsYXRlKDEsIDApIiAvPgo8L3N2Zz4K);\n    background-size: contain;\n    background-repeat: no-repeat;\n    top: -3px;\n    left: 0px;\n    position: relative;\n    vertical-align: text-bottom;\n    width: 8px;\n    height: 8px;\n    display: inline-block;\n  }\n\n  &:not(.canopy-open-link) { // if it is open, it has been inlined and isn't a cycle\n    .canopy-back-cycle-icon, .canopy-forward-cycle-icon, .canopy-up-cycle-icon {\n      top: 2px;\n      left: 1px;\n      text-decoration: none;\n      font-size: 9px;\n      position: relative;\n    }\n  }\n\n  &.canopy-open-link {\n    .canopy-back-cycle-icon, .canopy-forward-cycle-icon, .canopy-up-cycle-icon {\n      display: none;\n    }\n  }\n}\n\na.canopy-disabled-link {\n  color: #000000;\n  pointer-events: none;\n  cursor: not-allowed;\n  opacity: 0.5;\n  text-decoration: none;\n\n  .canopy-link-container {\n    border-color: #b0b0b0;\n  }\n}\n\na.canopy-selectable-link.canopy-linked-image {\n  text-decoration: none;\n\n  .canopy-link-container {\n    border: none;\n    background-color: inherit;\n  }\n\n  &.canopy-external-link .canopy-external-link-icon {\n    display: none;\n  }\n}\n\n/////////////////////////////////////////////\n\nhr.canopy-footnote-rule {\n  width: 750px;\n  display: block;\n  height: 1px;\n  border: 0;\n  border-top: 1px solid #ccc;\n  margin: 12px 0;\n  padding: 0;\n}\n\nsup {\n  font-size: 60%;\n  padding: 2px;\n}\n\n.canopy-footnote-span {\n  font-size: 80%;\n  display: block;\n  span { // text after number\n    margin-left: 2px;\n  }\n  &:not(:last-child) {\n    padding-bottom: 7px;\n  }\n}\n\nblockquote {\n  padding: 0 .7em;\n  color: #4b535b;\n  white-space: pre-wrap;\n  line-height: 150%; // makes links on sequential lines not overlap\n\n  a, a.canopy-selectable-link {\n    color: #4b535b;\n  }\n\n  &[dir=\"rtl\"] {\n    border-right: 0.25em solid lightgray;\n  }\n\n  &[dir=\"ltr\"] {\n    border-left: 0.25em solid lightgray;\n  }\n\n  margin-left: 0px;\n  margin-right: 0px;\n  margin-top: 15px;\n  margin-bottom: 15px;\n\n  /* a.canopy-selectable-link {\n    .canopy-link-content-container {\n      // reset button styling\n      background-color: #FFFFFF00;\n      padding-left: 0px;\n      padding-right: 0px;\n      border: none;\n      text-decoration: underline #D3D3D3;\n    }\n\n    &.canopy-selected-link.canopy-open-link .canopy-link-content-container {\n      background-color: #edeefc;\n      border: none;\n      text-decoration: none;\n    }\n\n    &.canopy-open-link .canopy-link-content-container {\n      background-color: #dedede;\n      border: none;\n      text-decoration: none;\n    }\n\n    &:hover:not(.canopy-open-link) .canopy-link-content-container {\n      background-color: #f0f0f0;\n      // text-decoration: underline #000000;\n    }\n  } */\n\n  .canopy-linebreak-span {\n    margin-bottom: 0px; // unset default\n  }\n\n  .canopy-linebreak-span.canopy-blank-linebreak {\n    height: 1em;\n  }\n\n  .canopy-blockquote-padded-linebreak { // we repla